import { Component, OnInit } from '@angular/core';
import { PedidoFaturadoService } from './pedidofaturado.service';
import { PedidoFaturado } from './pedidofaturado';

@Component({
  selector: 'app-pedidofaturado',
  templateUrl: './pedidofaturado.component.html',
  styleUrls: ['./pedidofaturado.component.scss']
})
export class PedidofaturadoComponent implements OnInit {

  pedido: Array<PedidoFaturado>;
  constructor(private _pedidoFaturadoService: PedidoFaturadoService) { };

  ngOnInit() {
    this._pedidoFaturadoService.getAll()
        .subscribe(suc => {
            this.pedido = suc;
        });
}
}
