import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Pedido } from './pedido';


@Injectable({
    providedIn: 'root'
  })

  export class PedidoService{

    url = 'http://app-pedidos-db1start1.herokuapp.com/api/pedidos?status=ABERTO';



  constructor(
    private _httpClient: HttpClient
  ) { }


  public getAll(){
    return this._httpClient.get<Array<Pedido>>(this.url);
  }

  }