import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Cliente } from './cliente';



@Injectable({
    providedIn: 'root'
  })

  export class ClienteService{

    url = 'http://app-pedidos-db1start1.herokuapp.com/api/clientes?status=ATIVO';



  constructor(
    private _httpClient: HttpClient
  ) { }


  public getAll(){
    return this._httpClient.get<Array<Cliente>>(this.url);
  }

  }