import { Component, OnInit } from '@angular/core';
import { PedidosCancelado } from './pedidocancelado';
import { PedidoCanceladoService } from './pedidocancelado.service';

@Component({
  selector: 'app-pedidocancelado',
  templateUrl: './pedidocancelado.component.html',
  styleUrls: ['./pedidocancelado.component.scss']
})
export class PedidocanceladoComponent implements OnInit {
  pedido: Array<PedidosCancelado>;
  constructor(private _pedidoCanceladoService: PedidoCanceladoService) { };

  ngOnInit() {
    this._pedidoCanceladoService.getAll()
        .subscribe(suc => {
            this.pedido = suc;
        });
}
}