import { PedidosCancelado } from './pedidocancelado';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
  })

  export class PedidoCanceladoService{

    url = 'http://app-pedidos-db1start1.herokuapp.com/api/pedidos?status=CANCELADO';



  constructor(
    private _httpClient: HttpClient
  ) { }


  public getAll(){
    return this._httpClient.get<Array<PedidosCancelado>>(this.url);
  }

  }