import { Component, OnInit } from '@angular/core';
import { Inativo } from './produtoinativo';
import { ProdutoInativoService } from './produtoinativo.service';


@Component({
  selector: 'app-produtoinativo',
  templateUrl: './produtoinativo.component.html',
  styleUrls: ['./produtoinativo.component.scss']
})
export class ProdutoinativoComponent implements OnInit {

  produto: Array<Inativo>;

  constructor(private _produtoInativoService: ProdutoInativoService) { };
  
  ngOnInit() {
      this._produtoInativoService.getAll()
          .subscribe(suc => {
              this.produto = suc;
          });
  }
}