import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { PedidoFaturado } from './pedidofaturado';



@Injectable({
    providedIn: 'root'
  })

  export class PedidoFaturadoService{

    url = 'http://app-pedidos-db1start1.herokuapp.com/api/pedidos?status=FATURADO';



  constructor(
    private _httpClient: HttpClient
  ) { }


  public getAll(){
    return this._httpClient.get<Array<PedidoFaturado>>(this.url);
  }

  }