import { Component, OnInit } from '@angular/core';
import { ProdutoExcluidoService } from './produtoexcluido.service';
import { Excluido } from './produtoexcluido';

@Component({
  selector: 'app-produtoexcluido',
  templateUrl: './produtoexcluido.component.html',
  styleUrls: ['./produtoexcluido.component.scss']
})
export class ProdutoexcluidoComponent implements OnInit {
  produtoExcuido: Array<Excluido>;
  constructor(private _produtoExcluidoService: ProdutoExcluidoService) { };

  ngOnInit() {
    this._produtoExcluidoService.getAll()
        .subscribe(suc => {
            this.produtoExcuido = suc;
        });
}
}


