import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ProdutoAtivo} from './produto';





@Injectable({
  providedIn: 'root'
})
export class ProdutoService {

  
  url = 'http://app-pedidos-db1start1.herokuapp.com/api/produtos?status=ATIVO';
 

  

  constructor(
    private _httpClient: HttpClient
  ) { }

  public getAll(){
    return this._httpClient.get<Array<ProdutoAtivo>>(this.url);
  }

 
  
  

  
}
