import { Inativo } from './produtoinativo';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';


@Injectable({
    providedIn: 'root'
  })

export class ProdutoInativoService {

  

  url = 'http://localhost:8080/api/produtos?status=INATIVO';

  

  constructor(
    private _httpClient: HttpClient
  ) { }

 

  public getAll(){
    return this._httpClient.get<Array<Inativo>>(this.url);
  }
  
  

  
}
