
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Excluido } from './produtoexcluido';


@Injectable({
    providedIn: 'root'
  })

export class ProdutoExcluidoService {

  

  url = 'http://app-pedidos-db1start1.herokuapp.com/api/produtos?status=EXCLUIDO';

  

  constructor(
    private _httpClient: HttpClient
  ) { }

 

  public getAll(){
    return this._httpClient.get<Array<Excluido>>(this.url);
  }
  
  

  
}
